$general-font: 'Open Sans';

$header-height: 46px;


/*  COLORS */
$color-green: rgba(61, 101, 86, .9);
$color-green-hover: rgba(36, 57, 49, .9);
$color-green-light: rgba(36, 171, 125, .9);
$color-green-light-hover: rgba(45, 148, 107, .9);
$color-white: #fff;
$color-white-alpha: rgba(255, 255, 255, .8);
$color-grey: #2b2b2b;
$color-disabled: rgba(116, 119, 118, 0.9);
$color-black-alpha: rgba(0, 0, 0, .2);
$color-red: #cd5f57;
$color-orange: #ffab56;

$color-black-bg: rgba(0, 0, 0, .8);

$color-invalid: rgba(255, 0, 11, .8);

$color-yellow: #fff17d;

$color-ava-green: #52be5d;
$color-ava-red: #ea5f56;
$color-ava-yellow: #eecf7e;

$color-red-state: #cc2128;
$color-green-state: #6cbb2d;






.profile-page {
  .profile-center{
    text-align: center;
  }
  .b{
    font-weight: bold;
  }
  .icon{
    background-position: center;
    background-repeat: no-repeat;
    height: 50px;
    width: 50px;
    margin: auto;
  }
  .win-icon {
    background-image: url(~publicImg/icons/all_win_icon.png);
    background-size: 100% 100%;
  }

  .all_game-icon {
    background-image: url(~publicImg/icons/all_game.png);
    background-size: 100% 100%;
  }

  .score-icon {
    background-image: url(~publicImg/icons/score_icon.png);
    background-size: 100% 100%;
  }

  .position-icon {
    background-image: url(~publicImg/icons/position_icon.png);
    background-size: 100% 100%;
  }
  .sub-text{
    font-size: 10px;
    color: #909090;
  }
  .score-text{
    font-size: 18px;
  }
  .avatar-icon{
    background-image: url(~publicImg/icons/position_icon.png);
    background-size: 100% 100%;
  }

  .right-col{
    padding-left: 0px;
  }

}



