$general-font: 'Open Sans';

$header-height: 46px;


/*  COLORS */
$color-green: rgba(61, 101, 86, .9);
$color-green-hover: rgba(36, 57, 49, .9);
$color-green-light: rgba(36, 171, 125, .9);
$color-green-light-hover: rgba(45, 148, 107, .9);
$color-white: #fff;
$color-white-alpha: rgba(255, 255, 255, .8);
$color-grey: #2b2b2b;
$color-disabled: rgba(116, 119, 118, 0.9);
$color-black-alpha: rgba(0, 0, 0, .2);
$color-red: #cd5f57;
$color-orange: #ffab56;

$color-black-bg: rgba(0, 0, 0, .8);

$color-invalid: rgba(255, 0, 11, .8);

$color-yellow: #fff17d;

$color-ava-green: #52be5d;
$color-ava-red: #ea5f56;
$color-ava-yellow: #eecf7e;

$color-red-state: #cc2128;
$color-green-state: #6cbb2d;






.games-page {
  .game-container {
    margin-top: 15px;
    margin-bottom: 15px;
  }
  h3 {
    margin-top: 10px;
    margin-bottom: 6px;
    position: relative;
  }
  .game-item {
    color: $color-white;
    .user-icon {
      float: left;
      width: 40px;
      height: 40px;
      margin: 10px 0;
    }
    .text-container {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 55px;
      right: 60px;
      h2 {
        margin-top: 2px;
        margin-bottom: 6px;
        font-size: 15px;
      }
      p {
        font-size: 12px;
      }
    }
    .time-container {
      float: right;
      width: 40px;
      height: 40px;
      margin: 10px 0;
      font-size: 10px;
    }
  }
  .right-text {
    position: absolute;
    display: block;
    top: -6px;
    right: 0;
    cursor: pointer;
    width: 30px;
    height: 30px;
    text-align: center;
    line-height: 30px;
    font-size: 20px;
  }

  .icon {
    display: block;
    width: 40px;
    height: 40px;
  }
}



