$general-font: 'Open Sans';

$header-height: 46px;


/*  COLORS */
$color-green: rgba(61, 101, 86, .9);
$color-green-hover: rgba(36, 57, 49, .9);
$color-green-light: rgba(36, 171, 125, .9);
$color-green-light-hover: rgba(45, 148, 107, .9);
$color-white: #fff;
$color-white-alpha: rgba(255, 255, 255, .8);
$color-grey: #2b2b2b;
$color-disabled: rgba(116, 119, 118, 0.9);
$color-black-alpha: rgba(0, 0, 0, .2);
$color-red: #cd5f57;
$color-orange: #ffab56;

$color-black-bg: rgba(0, 0, 0, .8);

$color-invalid: rgba(255, 0, 11, .8);

$color-yellow: #fff17d;

$color-ava-green: #52be5d;
$color-ava-red: #ea5f56;
$color-ava-yellow: #eecf7e;

$color-red-state: #cc2128;
$color-green-state: #6cbb2d;






#header-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  .status-bar {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: $header-height;
    background: #465153;
  }
  .open-menu-overflow {
    position: relative;
    width: 50px;
    height: 100%;
    cursor: pointer;
    float: left;
  }
  .menu-icon {
    position: absolute;
    width: 30px;
    height: 2px;
    background: white;
    top: 50%;
    left: 10px;
    &:before {
      top: -7px;
    }
    &:after {
      bottom: -7px;
    }
    &:after, &:before {
      content: "";
      position: absolute;
      display: block;
      height: 2px;
      background: white;
      width: 100%;
    }
  }
  .menu-container {
    position: absolute;
    top: 0;
    left: -200px;
    width: 200px;
    height: 100%;
    background: #465153;
    z-index: 2;
    transform: translate(0, 0);
    transition: transform 0.5s ease;
  }
  .back-menu {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    left: 100%;
    background: black;
    z-index: 1;
    opacity: 0;
    transition: opacity 0.5s ease;
  }
  &.menu-open {
    .menu-container {
      left: 0;
    }
    .back-menu {
      left: 0;
      opacity: 0.5;
    }
  }
}

.csstransforms #header-container {
  &.menu-open {
    .menu-container {
      transform: translate(200px, 0);
      left: -200px;
    }
  }
}



