$general-font: 'Open Sans';

$header-height: 46px;


/*  COLORS */
$color-green: rgba(61, 101, 86, .9);
$color-green-hover: rgba(36, 57, 49, .9);
$color-green-light: rgba(36, 171, 125, .9);
$color-green-light-hover: rgba(45, 148, 107, .9);
$color-white: #fff;
$color-white-alpha: rgba(255, 255, 255, .8);
$color-grey: #2b2b2b;
$color-disabled: rgba(116, 119, 118, 0.9);
$color-black-alpha: rgba(0, 0, 0, .2);
$color-red: #cd5f57;
$color-orange: #ffab56;

$color-black-bg: rgba(0, 0, 0, .8);

$color-invalid: rgba(255, 0, 11, .8);

$color-yellow: #fff17d;

$color-ava-green: #52be5d;
$color-ava-red: #ea5f56;
$color-ava-yellow: #eecf7e;

$color-red-state: #cc2128;
$color-green-state: #6cbb2d;






.profileDetail-page {
  .profileDetail-center{
    text-align: center;
  }
  .b{
    font-weight: bold;
  }
  .icon{
    background-position: center;
    background-repeat: no-repeat;
    height: 50px;
    width: 50px;
    margin: auto;
  }
  .win-icon {
    background-image: url(~publicImg/icons/all_win_icon.png);
    background-size: 100% 100%;
  }

  .all_game-icon {
    background-image: url(~publicImg/icons/all_game.png);
    background-size: 100% 100%;
  }

  .score-icon {
    background-image: url(~publicImg/icons/score_icon.png);
    background-size: 100% 100%;
  }

  .position-icon {
    background-image: url(~publicImg/icons/position_icon.png);
    background-size: 100% 100%;
  }
  .sub-text{
    font-size: 10px;
    color: #909090;
  }
  .score-text{
    font-size: 18px;
  }
  .avatar-icon{
    background-image: url(~publicImg/icons/position_icon.png);
    background-size: 100% 100%;
  }

  .padding0{
    padding-left: 0px;
    padding-right: 0px;
  }
  .padding5{
    padding-left: 7px;
    padding-right: 7px;
  }
  .topic{
    border-top: 1px solid black;
    //height: 30px;
  }



  .progress-bar {
    background-color: #1a1a1a;
    height: 15px;
    //padding: 5px;
    //width: 350px;
    //margin: 50px 0;
    -moz-border-radius: 15px;
    -webkit-border-radius: 15px;
    border-radius: 15px;
    -moz-box-shadow: 0 1px 5px #000 inset, 0 1px 0 #444;
    -webkit-box-shadow: 0 1px 5px #000 inset, 0 1px 0 #444;
    box-shadow: 0 1px 5px #000 inset, 0 1px 0 #444;
  }

  .progress-bar span {
    display: inline-block;
    height: 15px;
    //width: 200px;
    -moz-border-radius: 15px;
    -webkit-border-radius: 15px;
    border-radius: 15px;
    -moz-box-shadow: 0 1px 0 rgba(255, 255, 255, .5) inset;
    -webkit-box-shadow: 0 1px 0 rgba(255, 255, 255, .5) inset;
    box-shadow: 0 1px 0 rgba(255, 255, 255, .5) inset;
    -webkit-transition: width .4s ease-in-out;
    -moz-transition: width .4s ease-in-out;
    -ms-transition: width .4s ease-in-out;
    -o-transition: width .4s ease-in-out;
    transition: width .4s ease-in-out;
  }
  .blue span {
    background-color: #34c2e3;
  }

  .green span {
    background-color: #359035;
    background-image: -webkit-gradient(linear, left top, left bottom, from(#a5df41), to(#4ca916));
    background-image: -webkit-linear-gradient(top, #a5df41, #4ca916);
    background-image: -moz-linear-gradient(top, #a5df41, #4ca916);
    background-image: -ms-linear-gradient(top, #a5df41, #4ca916);
    background-image: -o-linear-gradient(top, #a5df41, #4ca916);
    background-image: linear-gradient(top, #a5df41, #4ca916);
  }
  .shine span {
    position: relative;
  }


}



