.btn {
  height: 43px;
  line-height: 38px;
  text-align: center;
  background-color: $color-green;
  border: 1px solid $color-white-alpha;
  color: $color-white;
  border-radius: 21px;
  font-size: 15px;
  transition: background-color 0.3s ease;
  font-family: $general-font;
  &.touchHover {
    background-color: $color-green-hover;
    //border-color: $color-green-hover;
  }
  &:focus {
    outline: none;
  }
  .size-small {
    height: 30px;
    font-size: 13px;
    line-height: 27px;
  }
  &.color-orange {
    background-color: $color-black-alpha;
    border-color: $color-orange;
    &.touchHover {
      background-color: $color-orange;
    }
  }
  &.single {
    margin-top: 15px;
    margin-bottom: 10px;
  }
  .btn-arrow {
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    width: 20px;
    background: url(~publicImg/button/arrow.png) no-repeat left center;
    background-size: 6px 10px;
  }
  .counter {
    position: absolute;
    display: inline-block;
    height: 20px;
    width: auto;
    padding: 0 8px;
    border-radius: 9px;
    background: $color-red;
    color: $color-white;
    text-align: center;
    border: 2px solid $color-white;
    top: 50%;
    margin-top: -10px;
    right: 10px;
    font-size: 12px;
    line-height: 16px;
  }
  &.disabled {
    background-color: $color-disabled;
    color: $color-grey;
    border-color: $color-grey;
  }
}
.btn-set {
  line-height: 43px;
  overflow: hidden;
  & > .btn {
    border-right-width: 1px;
    border-radius: 0;
    &:first-child {
      border-top-left-radius: 21px;
      border-bottom-left-radius: 21px;
    }
    &:last-child {
      border-top-right-radius: 21px;
      border-bottom-right-radius: 21px;
    }
  }
}

label.input {
  padding: 0;
  span {
    display: block;
    text-align: center;
    margin-bottom: 2px;
  }
  input{
    background-color: $color-white;
    border: 1px solid $color-black-alpha;
    border-radius: 21px;
    display: block;
    height: 38px;
    width: 100%;
    color: $color-grey;
    text-indent: 15px;
    font-size: 15px;
    font-family: $general-font;
    transition: background-color 0.3s ease;
    &:focus {
      outline: none;
      border: 1px solid $color-grey;
    }
    &.invalid {
      border-color: $color-invalid;
      box-shadow: 0 0 3px $color-invalid;
      color: $color-invalid;
    }
  }
}

.user-icon {
  position: relative;
  width: 46px;
  height: 46px;
  .avatar_bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 50%;
  }
  .avatar {
    position: relative;
    width: 100%;
    height: 100%;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
  }
  &[data-user-icon-status="0"] {
    .avatar_bg {
      background-color: $color-black-alpha;
    }
  }
  &[data-user-icon-status="1"] {
    .avatar_bg {
      background-color: $color-ava-green;
    }
  }
  &[data-user-icon-status="2"] {
    .avatar_bg {
      background-color: $color-ava-red;
    }
  }
  &[data-user-icon-status="3"] {
    .avatar_bg {
      background-color: $color-ava-yellow;
    }
  }
  @for $i from 1 through 50 {
    &[data-user-icon-id="#{$i}"] {
      .avatar {
        background-image: url('~publicImg/avatars/#{$i}.png');
      }
    }
  }
  @for $i from 101 through 150 {
    &[data-user-icon-id="#{$i}"] {
      .avatar {
        background-image: url('~publicImg/avatars/#{$i}.png');
      }
    }
  }
}


.status-icon {
  background-size: 100% 100%;
  @for $i from 0 through 17 {
    &[data-user-icon-id="#{$i}"] {
      background-image: url('~publicImg/smiles/smailey_'+ $i + '.png');
    }
  }
}

.list-container {
  color: $color-white;
  font-size: 15px;
  li {
    position: relative;
    width: 100%;
    overflow: hidden;
    padding: 0 7px;
    background-color: $color-green;
    border-left: 1px solid $color-grey;
    border-right: 1px solid $color-grey;
    &:after {
      content: "";
      display: block;
      position: absolute;
      bottom: 0;
      left: 10px;
      right: 10px;
      height: 1px;
      background-color: $color-grey;
    }
    &:first-child {
      border-top-left-radius: 21px;
      border-top-right-radius: 21px;
      border-top: 1px solid $color-grey;
    }
    &:last-child {
      border-bottom-left-radius: 21px;
      border-bottom-right-radius: 21px;
      border-bottom: 1px solid $color-grey;
      &:after {
        display: none;
      }
    }
    &.touchHover {
      background-color: $color-green-hover;
      &:after {
        display: none;
      }
    }
  }
}





