$general-font: 'Open Sans';

$header-height: 46px;


/*  COLORS */
$color-green: rgba(61, 101, 86, .9);
$color-green-hover: rgba(36, 57, 49, .9);
$color-green-light: rgba(36, 171, 125, .9);
$color-green-light-hover: rgba(45, 148, 107, .9);
$color-white: #fff;
$color-white-alpha: rgba(255, 255, 255, .8);
$color-grey: #2b2b2b;
$color-disabled: rgba(116, 119, 118, 0.9);
$color-black-alpha: rgba(0, 0, 0, .2);
$color-red: #cd5f57;
$color-orange: #ffab56;

$color-black-bg: rgba(0, 0, 0, .8);

$color-invalid: rgba(255, 0, 11, .8);

$color-yellow: #fff17d;

$color-ava-green: #52be5d;
$color-ava-red: #ea5f56;
$color-ava-yellow: #eecf7e;

$color-red-state: #cc2128;
$color-green-state: #6cbb2d;






.page {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  transition: opacity 0.2s ease;
  overflow: hidden;
  &.bg-blur {
    background: url(~publicImg/bg-blur.jpg) center;
    background-size: cover;
  }
  .scroll-content {
    position: relative;
    width: 100%;
    height: 100%;
    overflow: auto;
    overflow-x: hidden;
  }
  &.show {
    opacity: 1;
    transition: opacity 0.5s ease;
  }
  .loading-block {
    position: absolute;
    background: rgba(0, 0, 0, 0.9);
    top: 0 - $header-height;
    left: 200%;
    bottom: 0;
    width: 100%;
    opacity: 0;
    transition: opacity 0.2s ease;
    .loading-icon {
      position: absolute;
      background: url(~publicImg/loading.png);
      width: 112px;
      height: 112px;
      background-size: 100% 100%;
      transform-origin: 50% 50%;
      animation:rotate 5s linear infinite;
      animation-play-state: paused;
      top: 50%;
      left: 50%;
      margin-top: -56px;
      margin-left: -56px;
    }
    .text-block {
      position: absolute;
      top: 50%;
      margin-top: 70px;
      text-align: center;
      width: 100%;
    }
  }
  &.load {
    .loading-block {
      left: 0;
      opacity: 1;
      transition: opacity 1s ease 1s;
    }
    .loading-icon {
      animation-play-state: running;
    }
  }

}

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}



