html, body {
  width: 100%;
  height: 100%;
  min-width: 320px;
  overflow: hidden;
}
body {
  background-position: center;
  background-size: cover;
  color: white;
  background-image: url('~publicImg/auth_day_bg.jpg');
  transition: backgraund-image 0.5s ease;
  font-family: $general-font;
  &.auth {
    background-image: url('~publicImg/bg.jpg');
  }
}
* {
  box-sizing: border-box;
  user-select: none;
}
.table-middle-block {
  display: table;
  width: 100%;
  height: 100%;
}
.table-middle-cell{
  display: table-cell;
  vertical-align: middle;
}
#page-container {
  width: 100%;
  position: absolute;
  bottom: 0;
  top: $header-height;
}


h2 {
  font-size: 16px;
}



